.Card {
  align-items: center;
  background-color: var(--sonic-light-red);
  border: 15px solid var(--sonic-shoes);
  border-radius: 1.8rem;
  display: flex;
  flex-flow: column nowrap;
  height: 600px;
  justify-content: flex-end;
  padding: 1.3rem;
  position: relative;
  width: 337px;

  header {
    align-items: flex-end;
    display: flex;
    flex-flow: column nowrap;
    position: absolute;
    right: 0;
    text-align: right;
    top: 1rem;
    width: 95%;

    h2 {
      background-color: var(--sonic-fur);
      color: white;
      font-size: 1rem;
      overflow-wrap: break-word;
      padding: 0.5rem 1rem 0.5rem 8rem;
      text-transform: uppercase;
      top: 1rem;
      width: 100%;

      &:before {
        border-bottom: 28px solid transparent;
        border-left: 5px solid var(--sonic-light-red);
        border-right: 5px solid transparent;
        border-top: 28px solid var(--sonic-light-red);
        content: "";
        left: 0;
        position: absolute;
        top: 0;
      }
    }

    p {
      background-color: var(--sonic-yellow);
      padding: 0 1rem;
      position: relative;
      width: 160px;

      &:before {
        border-bottom: 11px solid var(--sonic-light-red);
        border-left: 5px solid var(--sonic-light-red);
        border-right: 5px solid transparent;
        border-top: 11px solid transparent;
        content: "";
        left: 0;
        position: absolute;
        top: 0;
      }
    }
  }
}

.Card-logo {
  filter: drop-shadow(0 0 3px rgb(31, 19, 90));
  left: 1.2rem;
  position: absolute;
  top: 0.5rem;
  width: 115px;
  z-index: 2;
}

.Card-img {
  height: 240px;
  object-fit: contain;
  object-position: center;
  width: 240px;
  z-index: 4;
}

.Card-desc-container {
  align-items: center;
  background-color: var(--sonic-shoes);
  border-radius: 1rem;
  color: var(--sonic-light-red);
  display: flex;
  height: 72px;
  justify-content: center;
  margin: 0.8rem 0;
  padding: 0.4rem;
  width: 100%;

  p {
    font-size: 0.85rem;
    max-height: 100%;
    overflow-wrap: break-word;
    text-align: center;
    width: 100%;
  }
}

.Card ul {
  list-style-type: none;
  padding-bottom: 40px;
  position: relative;
  width: 94%;

  li {
    background-color: var(--sonic-fur);
    border-radius: 0.3rem;
    display: flex;
    padding: 0.3rem 0;
    position: relative;
    transform: skew(-15deg);
    width: 100%;

    &:not(:first-child) {
      margin-top: 0.8rem;
    }
  }

  .Card-attr-icon {
    align-items: center;
    background-color: white;
    border: 3px solid var(--sonic-fur);
    border-radius: 0.3rem;
    color: var(--sonic-shoes);
    display: flex;
    font-size: 1.7rem;
    height: 40px;
    justify-content: center;
    left: 0;
    position: absolute;
    top: 0;
    width: 40px;
  }

  .Card-attr-text {
    color: white;
    padding-left: 3rem;
    width: 52%;
  }

  .Card-attr-gauge-container {
    display: flex;
    flex-flow: column nowrap;
    flex-grow: 1;
    justify-content: center;
  }

  .Card-attr-gauge {
    background-color: var(--sonic-eyes);
    height: 70%;
    transition: 400ms;
    transition-property: width;
  }

  .Card-attr-value {
    color: var(--sonic-yellow);
    font-weight: 600;
    padding: 0 0.5rem;
  }
}

.Card-trunfo {
  align-items: center;
  bottom: 0;
  display: flex;
  justify-content: flex-start;
  left: 0;
  position: absolute;
  width: 100%;
}

.Card-trunfo-icon {
  color: var(--sonic-eyes);
  display: flex;
  font-size: 1.5rem;
}

.Card-trunfo-text {
  font-weight: 600;
  margin-left: 0.5rem;
}
